import React, {useState,useEffect} from 'react';
import {BrowserRouter as Router,Link, Route, useRouteMatch}from 'react-router-dom';
import home from'./home';
import './app1.css';
import Axios from 'axios';
import Datepicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

function Contact()
{
const[namee,setname] = useState("");
const[ages,setage] = useState(0);
const[selecteddate,setdate] = useState(null);
const[emails,setemail] = useState("");
const[mobilenumber,setmobilenumber] = useState(0);
const[result,setresult]=useState([]);

Axios.defaults.baseURL = 'http://localhost:3001';

useEffect(()=>{
Axios.get("/Contact/get",function(err,res){
console.log(res.data);
})
},[result])

const Submit = (event) =>
{
Axios.post('/Contact/submit',{name:namee,age:ages,email:emails,dob:selecteddate,mn:mobilenumber});
event.preventDefault();
alert("data successfully inserted..");
}
    return(
	<div>
	<div className="container-fluid ">
	<header>
	<nav className="navbar navbar-expand-lg" id="navbar">
    <h2>developer zone</h2>
		<button id="bt" className="navbar-toggler ms-auto" data-bs-toggle="collapse" data-bs-target="#menu">
			<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-list" viewBox="0 0 16 16">
			<path fill-rule="evenodd" d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z"/>
			</svg>
		</button>
	<div className="collapse navbar-collapse" id="menu">
	<ul className="navbar-nav ms-auto">
	<li className="nav-item">
	<Link className="nav-link" to="/">Home</Link>
	</li>
	<li className="nav-item">
	<Link className="nav-link" to="/product">Product</Link>
	</li>
	<li className="nav-item">
	<Link className="nav-link" to="/capablities">Capablities</Link>
	</li>
	<li className="nav-item">
	<Link className="nav-link" to="/about">About Us</Link>
	</li>
	<li className="nav-item">
	<Link className="nav-link" to="/careers">Careers</Link>
	</li>
	<li className="nav-item">
	<Link className="nav-link" to="/contact">Contact Us</Link>
	</li>
	</ul>
	</div> 
	</nav>
	</header>
	<section id="sec1" class="ccontainer-fluid">
	<div class="row">
	<div class="col-lg-6 text-center py-lg-5 ">
	<form id="form1" method="get" class="form-group">
	Enter your Name<input id="name" type="text" class="form-control" name="name" onChange={(e)=>{setname(e.target.value)}}>
	</input>
	Enter your Age<input id="age" type="text" class="form-control" onChange={(e)=>{setage(e.target.value)}}>
	</input>
	Enter your Date of Birth<Datepicker selected={selecteddate} onChange={(date)=>{setdate(date)}}></Datepicker>
	Enter your Emailf<input name="email" type="email" required class="form-control" onChange={(e)=>{setemail(e.target.value)}}></input>
	Enter your Mobile Number<input name="mobilenumber" type="number" class="form-control" onChange={(e)=>{setmobilenumber(e.target.value)}}></input>
	<button type="submit" name="data" onClick={Submit} class="btn btn-success">Submit</button>
	<button  class="btn btn-primary">Get</button>
	</form>
	</div>
	<div class="col-lg-6">
	{result.map((val)=>{
	return <h1>name:{val.name}</h1>
	})
	}
	</div>
	</div>
	</section>
	</div>
	</div>
    );
  }

export default Contact;
