import React from 'react';
import {BrowserRouter as swith,Link, Route,NavLink, useRouteMatch}from 'react-router-dom';
import resume from'./resume';
import resume1 from'./resume1';
function navbar()
{
return(
<div className="container-fluid ">
	<nav className="navbar navbar-expand-lg" id="navbar">
    <h2>developer zone</h2>
		<button id="bt" className="navbar-toggler ms-auto" data-bs-toggle="collapse" data-bs-target="#menu">
			<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-list" viewBox="0 0 16 16">
			<path fill-rule="evenodd" d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z"/>
			</svg>
		</button>
	<div className="collapse navbar-collapse" id="menu">
	<ul className="navbar-nav ms-auto">
	<li className="nav-item">
	<NavLink className="nav-link" to="/">Home</NavLink>
	</li>
	<li className="nav-item">
	<NavLink className="nav-link" to="/resume">service</NavLink>
	</li>
	<li className="nav-item">
	<NavLink className="nav-link" to="/resume1">about</NavLink>
	</li>
	</ul>
	<Route path="/resume" component="resume"/>
	</div>
	</nav>
	</div>
	);
}
export default navbar;